// common css

.icon {
  margin-left: 5px;
  color : #ebb7aa;
}

.App {
  max-width: 500px; width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 20px;
  // border: 1px solid #808080;
  display: flex;
  flex-direction: column;
  gap: 30px;
  .Header {
    h3 {
      display : flex; align-items: center;
      &:last-child {
        font-size : 32px; color: #1f93ff;
        margin-top : 20px;
        font-weight : 700;
      }
    }
  }
  .TodoEditor{
    h4 {
      display: flex; align-items: center;
    }
    .editor_wrapper {
      width: 100%; display: flex; gap: 10px;
      margin-top: 20px;
      input {
        flex: 1; box-sizing: border-box;
        border: 1px solid rgb(220, 220, 220);
        border-radius : 5px;
        padding: 15px;
        &:focus {
          outline:none;
          border: 1px solid #1f93ff;
        }
      }
      button {
        cursor: pointer; width: 80px; border: none;
        border-radius: 5px;
        background-color: #1f93ff; color: #fff; font-weight: bold;
      }
    }
  }
  .TodoList {
    h4 {
      display: flex; align-items: center;
    }
    .searchbar {
      margin-bottom: 20px;
      width: 100%; border: none; border-bottom: 1px solid rgb(220, 220, 220);
      box-sizing: border-box;
      padding-top: 15px; padding-bottom: 15px;
      &:focus {
        outline: none;
        border-color : #1f93ff;
      }
    }
    .list_wrapper {
      display: flex; flex-direction: column; gap:20px;
      .TodoItem {
        display: flex; align-items: center; gap: 20px;
        padding-bottom: 20px;
        border-bottom:1px solid rgb(240, 240, 240);
        .checkbox_col {
          width: 20px;
          
        }
        .title_col {
          flex:1;
        }
        .title_gray {
          flex:1;
          text-decoration: line-through;
          color : #ccc;
        }
        .date_col {
          font-size: 14px; color:gray;
        }
        .btn_col button {
          cursor: pointer; color:gray; font-size: 14px;
          border: none; border-radius: 5px; padding:5px;
        }
      }
    }
  }
}

